import {Paper, Typography} from "@mui/material";
import LogoIcon from "../../icon/Logo";
import React from "react";
import "./about.scss"
import {makeStyles} from "tss-react/mui";

const useStyles = makeStyles()((theme) => ({

    aboutLogoIcon: {
        color: theme.palette.secondary.dark,
        background:theme.palette.secondary.light,
        boxShadow:theme.shadows[5],
    },
}));


const AboutProject = () => {




    const {cx, classes} = useStyles();

    return (
        <div className={"about"}>
            <LogoIcon className={cx(classes.aboutLogoIcon, "about_logo")}/>
            <Typography className={"big_title"} component={"h1"} variant={"h3"}>
                 
            </Typography>
            <Typography className={"subtitle"} variant={"h6"}>  </Typography>

            <Typography  >
                <Typography variant={"subtitle1"} component={"span"}>" :   "</Typography> -   -,   ,  
                     .   :
            </Typography>


        </div>

    )


}

export default AboutProject;